.App-header {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 10000;
  width: 100vw;
  background-color: #ffffff;
  min-height: 5vmin;
  padding: 0 20px;
  box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.05);
}

.App-header {
  height: 60px;
}

.Header-logo {
  font-size: 1.6rem;
  color: #1f1f1f;
}

.App-header .Header-login {
  display: flex;
  position: relative;
  font-size: 20px;
  color: #1f1f1f;
  border: none;
  outline: 0;
  box-shadow: 0;
  margin-left: 1700px;
  margin-bottom: 10px;
}
